import React, { useEffect, useCallback } from "react";
import { createPortal } from "react-dom";
import logo from "../../logo.png";
import { FiX } from "react-icons/fi";
import { SlLocationPin } from "react-icons/sl";
import { LocationButton } from "./Modal.styled";
import { IconContext } from "react-icons";
import {
  ModalBackdrop,
  ModalContent,
  Container,
  ModalHeader,
  ModalButton,
  ModalList,
  ModalTitle,
  ModalTextContainer,
  ModalText,
  ModalSubmitBtn,
  NavLinkStyled,
} from "./Modal.styled";

const modalRoot = document.querySelector("#root");

export default function Modal({ onClose, children }) {
  useEffect(() => {
    const handleKeyDown = (e) => {
      if (e.code === "Escape") {
        onClose();
      }
    };

    window.addEventListener("keydown", handleKeyDown);

    return () => {
      window.removeEventListener("keydown", handleKeyDown);
    };
  }, [onClose]);

  const handleBackdropClick = useCallback(
    (event) => {
      if (event.currentTarget === event.target) {
        onClose();
      }
    },
    [onClose]
  );

  return createPortal(
    <ModalBackdrop onClick={handleBackdropClick}>
      <ModalContent onClose={onClose}>
        <Container>
          <ModalHeader>
            <img src={logo} width={120} alt="logo" />
            <ModalButton onClick={onClose}>
              <IconContext.Provider
                value={{
                  size: "30px",
                  color: "#007586",
                }}
              >
                <FiX />
              </IconContext.Provider>
            </ModalButton>
          </ModalHeader>
          <LocationButton
            href="https://goo.gl/maps/o3qvsXRkfv8h3hdw5"
            target="_blank"
          >
            <IconContext.Provider
              value={{
                size: "25px",
                color: "#007586",
              }}
            >
              <SlLocationPin />
            </IconContext.Provider>
            <ModalText>., 121, .</ModalText>
          </LocationButton>
          <ModalTextContainer>
            <ModalText href="tel:+380966193616">+38 (093) 619 3616</ModalText>
          </ModalTextContainer>
          <ModalList>
            <ModalTitle onClick={onClose}>
              <NavLinkStyled to={"/"}></NavLinkStyled>
            </ModalTitle>
            <ModalTitle onClick={onClose}>
              <NavLinkStyled to={"/portfolio"}></NavLinkStyled>
            </ModalTitle>
            <ModalTitle onClick={onClose}>
              <NavLinkStyled to={"/reviews"}></NavLinkStyled>
            </ModalTitle>
            <ModalTitle onClick={onClose}>
              <NavLinkStyled to={"/price"}></NavLinkStyled>
            </ModalTitle>
          </ModalList>
          <ModalSubmitBtn></ModalSubmitBtn>
        </Container>
      </ModalContent>
    </ModalBackdrop>,
    modalRoot
  );
}
